<template>
  <div class="check_content">
    <start-check ref="check" @finishCheck="finishCheck"></start-check>
    <el-row class="tac">
      <el-col :span="$store.state.leftCol" :class="leftClass" ref="leftRef">
        <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
        >
          <el-submenu v-for="(item,index) in navMenuData" :key="index" :index="String(index+1)">
            <template slot="title">
              <i class="iconfont icon_cla" :class="item.iconType"></i>
              <span>{{item.navName}}</span>
            </template>
            <el-menu-item
              v-for="(itm,idx) in item.children"
              :key="idx"
              @click="toPath(itm.path,itm.flag)"
              :index="String(`${(index+1)}`+'-'+`${(idx+1)}`)"
            >
              <div @contextmenu="showMenu(item.navName,itm.name)">
                {{itm.name}}
                <span :class="itm.tipsClass">{{itm.tips}}</span>
              </div>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-col>
      <el-col :span="$store.state.rightCol">
        <router-view></router-view>
      </el-col>
    </el-row>
    <vue-context-menu :contextMenuData="contextMenuData" @checkThis="checkThis" @leading="leading"></vue-context-menu>
  </div>
</template>
<script>
import startCheck from "@/components/startCheck";

export default {
  components: {
    startCheck
  },
  data() {
    return {
      navMenuData: [
        {
          iconType: "icon-zhongduanshenhe-",
          navName: "终端基本信息",
          children: [
            {
              path: "/checkInfo",
              name: "检查信息",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/hostInfo",
              name: "主机信息",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/hardDiskInfo",
              name: "硬盘信息",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/operatingSystem",
              name: "操作系统",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/identification",
              name: "身份鉴别",
              tips: "",
              tipsClass: "",
              flag: false
            }
          ]
        },
        {
          iconType: "icon-usb",
          navName: "USB设备检查",
          children: [
            {
              path: "/storageDevice",
              name: "存储设备",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/otherEquipment",
              name: "其他设备",
              tips: "",
              tipsClass: "",
              flag: false
            }
          ]
        },
        {
          iconType: "icon-hulianwang",
          navName: "联网记录检查",
          children: [
            {
              path: "/browserRecords",
              name: "浏览器记录",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/cookieRecords",
              name: "Cookies记录",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/onlineRecords",
              name: "上网软件记录",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/emailRecords",
              name: "邮件收发记录",
              tips: "",
              tipsClass: "",
              flag: false
            }
          ]
        },
        {
          iconType: "icon-jiancha",
          navName: "软件安装检查",
          children: [
            {
              path: "/systemsSoftware",
              name: "系统软件",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/mailClient",
              name: "邮件客户端",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/virtualSoft",
              name: "虚拟机软件",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/antivirusSoft",
              name: "杀毒软件信息",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/imageFile",
              name: "镜像文件",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/antiSoft",
              name: "反取证软件",
              tips: "",
              tipsClass: "",
              flag: false
            }
          ]
        },
        {
          iconType: "icon-fuzhuguanli",
          navName: "其他辅助检查",
          children: [
            {
              path: "/documentUse",
              name: "文件使用记录",
              tips: "",
              tipsClass: "",
              flag: false
            },
            {
              path: "/switchingRecord",
              name: "开关机记录",
              tips: "",
              tipsClass: "",
              flag: false
            }
          ]
        }
      ],
      type: {
        navName: "",
        name: ""
      },
      leftClass: "small_style",
      // contextmenu data (菜单数据)
      contextMenuData: {
        // the contextmenu name(@1.4.1 updated)
        menuName: "demo",
        // The coordinates of the display(菜单显示的位置)
        axis: {
          x: null,
          y: null
        },
        // Menu options (菜单选项)
        menulists: [
          {
            fnHandler: "checkThis", // Binding events(绑定事件)
            icoName: "fa fa-home fa-fw", // icon (icon图标 )
            btnName: "检查此项(O)" // The name of the menu option (菜单名称)
          },
          {
            fnHandler: "leading",
            disabled: true,
            icoName: "fa fa-home fa-fw",
            btnName: "导出报告(R)"
          }
        ]
      },
      tips: "", //检查状态提示
      tipClass: "checking",
      path: ""
    };
  },
  methods: {
    handleOpen() {
      // key, keyPath
      // console.log(key, keyPath);
    },
    handleClose() {
      // key, keyPath
      // console.log(key, keyPath);
    },
    toPath(path, flag) {
      // console.log(path, flag);
      this.path = path;
      this.$router.push({ path: path, query: { flag: flag } });
    },
    showMenu(navName, name) {
      this.type = {
        navName,
        name
      };
      // console.log(this.type);
      event.preventDefault();
      // event.stopPropagation();
      var x = event.clientX;
      var y = event.clientY;
      // Get the current location
      this.contextMenuData.axis = {
        x,
        y
      };
    },
    checkThis() {
      this.navMenuData.forEach((item, index) => {
        if (this.type.navName == item.navName) {
          item.children.forEach((itm, idx) => {
            if (this.type.name == itm.name) {
              this.navMenuData[index].children[idx].tips = "(正在检查)";
              this.navMenuData[index].children[idx].tipsClass = "checking";
            }
          });
        }
      });
      this.$refs.check.startCheck(this.type.name);
    },
    leading() {
      alert("导出文件");
    },
    finishCheck(val) {
      this.navMenuData.forEach((item, index) => {
        if (this.type.navName == item.navName) {
          item.children.forEach((itm, idx) => {
            if (this.type.name == itm.name) {
              this.navMenuData[index].children[idx].tips = val;
              this.navMenuData[index].children[idx].tipsClass = "finish";
              this.navMenuData[index].children[idx].flag = "true";
              this.toPath(this.navMenuData[index].children[idx].path, true);
            }
          });
        }
      });

      // console.log(val);
      // this.tipClass = "finish";
      // this.tips = val;
    }
  }
};
</script>

